#header{
    width: 100%;
    height: 100%;
    position: relative;
    background-color: #fff;
}
#header #language-div{
    width: 220px;
    height: 64.1px;
    border-left: 4px solid #f6f8fb;
    border-right: 4px solid #f6f8fb;
    margin: 24px 27px 27px 0px;
    float: right;
    position: relative;
}
#header #language-div #language-icon{
    position: absolute;
    top: 17px;
    left: 32px;
    font-size: 30px;
    fill: #73778d;
    /* border: 1px solid red; */
}
#header #language-div div{
    position: absolute;
    top: 16px;
    left: 83px;
    width: 63px;
    height: 25.4px;
    font-family: NotoSans;
    font-size: 21.4px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    letter-spacing: normal;
    color: #73778d;
    display: inline-block;
    /* border: 1px solid red; */

}
#header #language-div #arrow-icon{
    /* border: 1px solid red; */

    position: absolute;
    top: 20px;
    left: 166px;
    font-size: 25px;
    fill: #73778d;
}
#header #logo-div{
    width: 238px;
    height: 52px;
    margin: 32px 35px 35px 0;
    float: right;
    /* border: 1px solid red; */

}

#header #logo-div #logo{
    width: 100%;
}
#header #alram-div{
    width: 40px;
    height: 40px;
    margin: 38px 35px 0 0;
    float: right;

}
#header #alram-div .icon{
    font-size: 35px;
    fill: #73778d;

}