#login{
    width: 100%;
    height: 100%;
    padding: 48px 0 0 48px;
    background-color: #72a9fc;
    position: relative;
}

#top #logo{
    width: 217px;
    margin: 9.6px 0 0 0;
}
#top #title-div{
    position: absolute;
    right: 35px;
    top: 61px;
    width: 360.7px;
    height: 56px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 13.3px;
    margin: 0 35.3px 20.1px 124.5px;
    padding: 13.3px;
}
#top #title{
    width: 334px;
    height: 43px;
    flex-grow: 0;
    -webkit-text-stroke: 0.3px #72a9fc;
    font-family: NotoSans;
    font-size: 32px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: #fff;
}

#middle #login-icon-div{
    position: absolute;
    width: 166.75px;
    height: 166.75px;
    left: 876.19px;
    top: 124.06px;
    border-radius: 50%;
    background-color: #fdfeff;
    text-align: center;
    z-index: 2;
}
#middle #login-icon{
    width: 102.9px;
    height: 101.6px;
    fill: #72a9fc;
    margin-top: 28px;
}
#middle .login-div{
    position: absolute;
    width: 880.5px;
    height: 868.5px;
    left: 519px;
    top: 207px;
    flex-grow: 0;
    padding: 80px 203.1px 32.6px 213.1px;
    border-radius: 53.4px;
    box-shadow: 0 5.3px 5.3px 0 rgba(0, 0, 0, 0.07);
    background-color: #fff;
    text-align: center;
}
#middle #size{
    width: 453px;
    height: 755.81px;
    /* border: 1px solid red; */
}
#middle .title{
    width: 186.8px;
    height: 84px;
    font-family: NotoSans;
    font-size: 61.4px;
    font-weight: bold;
    color: #558eff;
}
#middle .input-div{
    /* position: absolute; */
    width: 452px;
    height: 50.7px;
    flex-grow: 0;
    text-align: left;
    border-bottom: #72a9fc 5.3px solid;
}
#middle #id-input-div{
    margin: 79px 0 40px 1px;
}
#middle #password-input-div{
    margin: 26.7px 0 0px 1px;
}
#middle .input-div .icon{
    font-size: 37.1px;
    fill: #72a9fc;
    float: left;
    margin-left: -3px;
    margin-right: 26px;
}
#middle .input-div .input{
    height: 32px;
    font-family: NotoSans;
    font-size: 24px;
    font-weight: 600;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    color: #828282;
    border: none;
    float: left;    /*안그러면 아래로 내려감*/
    outline-style: none; /* 포커스시 발생하는 효과 제거를 원한다면 */ 
    -webkit-appearance: none; /* 브라우저별 기본 스타일링 제거 */
}
#middle #password-input-div #show-icon-div{
    display: inline;
    height: 32px;
}
#middle #fail-message{
    width: 453px;
    height: 23px;
    margin: 7px 0 10px 0;
    font-family: Noto Sans;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 22px;
    display: flex;
    align-items: center;
    color: #E82B2B;
}
#middle #password-input-div #show-icon{
    font-size: 40px;
    fill: #8f8f8f;
}
#middle #remember-label #checkbox{
    width: 22px;
    height: 22px;
    flex-grow: 0;
    margin: 0 9px 0 0;
    border-radius: 2.7px;
    border: solid 2px #969696;
    background-color: #fff;
    -webkit-appearance: none; /* 브라우저별 기본 스타일링 제거 */

}
#middle #remember{
    float: left;
    margin-bottom: 32.6px;
}
/*input 은 숨겨주기*/
input#checkbox{
    display:none;
}
/*input 바로 다음의 label:before 에 체크하기 전 CSS 설정*/
input#checkbox + label:before{
    content:"";
    display:inline-block;
    line-height:20px;
    width: 22px;
    height: 22px;
    flex-grow: 0;
    margin: 0 9px 0 0;
    border-radius: 2.7px;
    border: solid 2px #969696;
    vertical-align:middle;/*체크 전과 체크 후 높이 차이 때문에 설정*/
}

/*checked된 input 바로 다음의 label:before 에 체크 후 CSS 설정*/  
input#checkbox:checked + label:before{
    content:'✔';
    color:#fff;
    background-color:#000;
    border-color:#000;
    font-size:17px;
    text-align:center;
}
#middle #remember #text{
    line-height:26px;
    height: 24px;
    flex-grow: 0;
    font-size: 22px;
    font-weight: bold;
    text-align: left;
    color: #8f8f8f;
    float: right;    /*안그러면 아래로 내려감*/

}

#middle #login-button{
    width: 452.2px;
    height: 73.4px;
    flex-grow: 0;
    border: none;

    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 13.3px;
    margin: 0px 0.8px 53.4px 0;
    padding: 14.7px 181.4px;
    border-radius: 6.7px;
    box-shadow: 0 5.3px 5.3px 0 rgba(0, 0, 0, 0.1);
    background-color: #72a9fc;
    text-align: center;
}

#middle #login-button:hover {
    background-color: #104ca5;
}
#middle  #button-text{
    width: 89.4px;
    height: 44px;
    background-color: #ffffff00;
    flex-grow: 0;
    font-family: NotoSans;
    font-size: 32px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: #fff;
}
#middle #forgot{
    width: 220.1px;
    height: 33.4px;
    flex-grow: 0;
    margin: 53.4px 232.9px 81.9px 0;
    font-family: NotoSans;
    font-size: 24px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: left;
    color: #8f8f8f;
}
#middle #logo{
    width: 341px;
}