#side-nav{
    width: 100%;
    height: 100%;
    position: relative;
    box-shadow: 5.3px 0 21.4px 0 rgba(16, 30, 115, 0.08);
    background-color: #fff;
}
#side-nav #top{
    border: 1px solid #ffffff00;
}
#side-nav #top .user-div{
    margin: 0px;
    padding: 0px;
    height: 68px;
    margin: 77px 0 0 41px ;
}
#side-nav #top #nav-user-logo-div{
    width: 68px;
    height: 68px;
    float: left;}

#side-nav #top #nav-user-logo{
    width: 100%;
    height: 100%;
    padding: 0px;
}
#side-nav #top #nav-user-name{
    float: left;
    width: 125px;
    height: 50px;
    margin: 9px 0 0 22.7px;
    font-family: NotoSans;
    font-size: 21.4px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    /* line-height: 68px; */
    letter-spacing: normal;
    text-align: left;
    color: #525358;
}
#side-nav .side-class{
    text-align: left;
    padding-left:46.7px;
    margin: 70px 0 10px 0;
    font-family: NotoSans;
  font-size: 18.7px;
  font-weight: 600;
  color: #336cfb;

}
#side-nav .nav-item{
    
    width: 100%;
    height: 70.6px;
    flex-grow: 0;
    padding: 18px 0 18px 46.7px;
    display: block;    
    text-align: left;
    
}
.side-nav > .nav-active{
    background-color: rgba(235, 240, 255, 0.4);
    border-left: 14.7px solid #336cfb; 
}
.side-nav > .nav-active .icon{
    fill: #558EFF;
}
#side-nav .nav-item p{
    line-height: 35px;
    display:inline-block;
    height: 29.3px;
    flex-grow: 0;
    font-family: NotoSans;
    font-size: 24px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    letter-spacing: normal;
    text-align: center;
    color: #73778d;
}
#side-nav .nav-item .icon{
    display:inline-block;
    float: left;
    font-size: 30px;
    fill: #73778D;
}
#side-nav #bottom #nav-login{
    width: 188.7px;
    height: 47.8px;
    flex-grow: 0;
    margin: 111.8px 52.9px 0 48px;
    padding: 10px 0 10px 31px;
    border-radius: 23px;
    border: solid 2.2px #73778d;
    background-color: #fff;
}   
#side-nav #bottom #nav-login .icon{

    display:inline-block;
    float: left;
    font-size: 20px;
    margin: 0 -30px 0 0;
    fill: #73778d;
}
#side-nav #bottom #nav-login p{
    /* float: left; */
    display:inline-block;
    /* width: 69.2px; */
    height: 25.9px;
    font-family: NotoSans;
    font-size: 18.4px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 25px;
    letter-spacing: normal;
    text-align: center;
    color: #73778d;
}
